// tests/integration/components/FactureForm.test.jsx
// Tests d'intégration pour le composant FactureForm

import React from 'react';
import { renderWithProviders, fillForm, submitForm, expectValidationError, expectNotification, screen, waitFor } from '../../utils/testUtils';
import { mockFactureService, mockClientService, mockTarificationService, resetAllMocks } from '../../mocks/services';

// Mocks des services
jest.mock('@services/FactureService', () => {
  return jest.fn().mockImplementation(() => mockFactureService);
});

jest.mock('@services/ClientService', () => {
  return jest.fn().mockImplementation(() => mockClientService);
});

jest.mock('@services/TarificationService', () => {
  return jest.fn().mockImplementation(() => mockTarificationService);
});

// Mock des hooks
jest.mock('@components/factures/hooks/useFactureFormActions', () => ({
  useFactureFormActions: () => ({
    chargerFacture: jest.fn().mockResolvedValue(global.createTestFacture()),
    fetchClientDetails: jest.fn().mockResolvedValue(global.createTestClient()),
    fetchProchainNumeroFacture: jest.fn().mockResolvedValue('005.2025'),
    submitFacture: jest.fn().mockResolvedValue({ success: true, idFacture: 1 }),
    sauvegarderFacture: jest.fn().mockResolvedValue({ success: true }),
    factureActions: mockFactureService,
    clientActions: mockClientService,
    tarifActions: { charger: jest.fn() }
  })
}));

// Import après les mocks
import FactureForm from '@components/factures/FactureForm';

describe('FactureForm', () => {
  const defaultProps = {
    mode: 'create',
    idFacture: null,
    clients: [createTestClient({ id: 1 }), createTestClient({ id: 2, nom: 'Martin' })],
    onRetourListe: jest.fn(),
    onFactureCreated: jest.fn(),
    onFactureModified: jest.fn()
  };

  beforeEach(() => {
    resetAllMocks();
    jest.clearAllMocks();
  });

  describe('Mode Création', () => {
    it('devrait afficher le formulaire en mode création', () => {
      renderWithProviders(<FactureForm {...defaultProps} />);
      
      expect(screen.getByText(/nouvelle facture/i)).toBeInTheDocument();
    });

    it('devrait afficher les champs obligatoires', () => {
      renderWithProviders(<FactureForm {...defaultProps} />);
      
      expect(screen.getByLabelText(/client/i)).toBeInTheDocument();
      expect(screen.getByLabelText(/date/i)).toBeInTheDocument();
    });

    it('devrait permettre de sélectionner un client', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      expect(clientSelect.value).toBe('1');
    });

    it('devrait valider les champs obligatoires avant soumission', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      // Tenter de soumettre sans remplir les champs
      await submitForm(user, 'Enregistrer');
      
      // Vérifier les messages d'erreur
      await waitFor(() => {
        expect(screen.getByText(/client.*obligatoire/i)).toBeInTheDocument();
      });
    });

    it('devrait créer une facture avec succès', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      // Remplir le formulaire
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      // Ajouter une ligne (simulation)
      // ... ajout de ligne
      
      // Soumettre
      await submitForm(user, 'Enregistrer');
      
      await waitFor(() => {
        expect(defaultProps.onFactureCreated).toHaveBeenCalled();
      });
    });
  });

  describe('Mode Modification', () => {
    const editProps = {
      ...defaultProps,
      mode: 'edit',
      idFacture: 1
    };

    it('devrait charger les données de la facture existante', async () => {
      renderWithProviders(<FactureForm {...editProps} />);
      
      await waitFor(() => {
        expect(screen.getByDisplayValue('001.2025')).toBeInTheDocument();
      });
    });

    it('devrait afficher le bouton Modifier', () => {
      renderWithProviders(<FactureForm {...editProps} />);
      
      expect(screen.getByRole('button', { name: /modifier/i })).toBeInTheDocument();
    });

    it('devrait modifier une facture avec succès', async () => {
      const { user } = renderWithProviders(<FactureForm {...editProps} />);
      
      // Modifier la ristourne
      const ristourneInput = screen.getByLabelText(/ristourne/i);
      await user.clear(ristourneInput);
      await user.type(ristourneInput, '50');
      
      // Soumettre
      await submitForm(user, 'Modifier');
      
      await waitFor(() => {
        expect(defaultProps.onFactureModified).toHaveBeenCalled();
      });
    });
  });

  describe('Mode Visualisation', () => {
    const viewProps = {
      ...defaultProps,
      mode: 'view',
      idFacture: 1
    };

    it('devrait afficher les données en lecture seule', async () => {
      renderWithProviders(<FactureForm {...viewProps} />);
      
      await waitFor(() => {
        const inputs = screen.getAllByRole('textbox');
        inputs.forEach(input => {
          expect(input).toHaveAttribute('readonly');
        });
      });
    });

    it('ne devrait pas afficher le bouton Enregistrer', () => {
      renderWithProviders(<FactureForm {...viewProps} />);
      
      expect(screen.queryByRole('button', { name: /enregistrer/i })).not.toBeInTheDocument();
    });

    it('devrait afficher le bouton Retour', () => {
      renderWithProviders(<FactureForm {...viewProps} />);
      
      expect(screen.getByRole('button', { name: /retour/i })).toBeInTheDocument();
    });
  });

  describe('Gestion des lignes de facture', () => {
    it('devrait permettre d\'ajouter une ligne', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      const addButton = screen.getByRole('button', { name: /ajouter.*ligne/i });
      await user.click(addButton);
      
      // Vérifier qu'une nouvelle ligne a été ajoutée
      await waitFor(() => {
        expect(screen.getAllByTestId(/ligne-facture/i).length).toBeGreaterThan(0);
      });
    });

    it('devrait permettre de supprimer une ligne', async () => {
      const propsWithLines = {
        ...defaultProps,
        mode: 'edit',
        idFacture: 1
      };
      
      const { user } = renderWithProviders(<FactureForm {...propsWithLines} />);
      
      await waitFor(() => {
        expect(screen.getByTestId('ligne-facture-0')).toBeInTheDocument();
      });
      
      const deleteButton = screen.getByTestId('delete-ligne-0');
      await user.click(deleteButton);
      
      // Confirmer la suppression si nécessaire
      // ...
    });

    it('devrait recalculer le total lors de la modification d\'une ligne', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} mode="edit" idFacture={1} />);
      
      await waitFor(() => {
        expect(screen.getByTestId('ligne-facture-0')).toBeInTheDocument();
      });
      
      // Modifier la quantité
      const quantiteInput = screen.getByTestId('quantite-ligne-0');
      await user.clear(quantiteInput);
      await user.type(quantiteInput, '5');
      
      // Vérifier que le total a été recalculé
      await waitFor(() => {
        const total = screen.getByTestId('total-facture');
        expect(total).toHaveTextContent(/2500/); // 5 * 500
      });
    });
  });

  describe('Validation du formulaire', () => {
    it('devrait valider le format de la date', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      const dateInput = screen.getByLabelText(/date.*facture/i);
      await user.clear(dateInput);
      await user.type(dateInput, 'invalid-date');
      
      await submitForm(user, 'Enregistrer');
      
      await waitFor(() => {
        expect(screen.getByText(/date.*invalide/i)).toBeInTheDocument();
      });
    });

    it('devrait valider que la ristourne ne dépasse pas le total', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} mode="edit" idFacture={1} />);
      
      await waitFor(() => {
        expect(screen.getByLabelText(/ristourne/i)).toBeInTheDocument();
      });
      
      const ristourneInput = screen.getByLabelText(/ristourne/i);
      await user.clear(ristourneInput);
      await user.type(ristourneInput, '99999'); // Plus que le total
      
      await submitForm(user, 'Modifier');
      
      await waitFor(() => {
        expect(screen.getByText(/ristourne.*dépasse/i)).toBeInTheDocument();
      });
    });

    it('devrait valider qu\'il y a au moins une ligne', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      // Sélectionner un client mais pas de lignes
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      await submitForm(user, 'Enregistrer');
      
      await waitFor(() => {
        expect(screen.getByText(/au moins une ligne/i)).toBeInTheDocument();
      });
    });
  });

  describe('Navigation et annulation', () => {
    it('devrait demander confirmation avant de quitter avec des modifications non sauvegardées', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      // Faire une modification
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      // Cliquer sur Retour
      const retourButton = screen.getByRole('button', { name: /retour/i });
      await user.click(retourButton);
      
      // Vérifier que le modal de confirmation apparaît
      await waitFor(() => {
        expect(screen.getByText(/modifications non sauvegardées/i)).toBeInTheDocument();
      });
    });

    it('devrait permettre d\'annuler et revenir au formulaire', async () => {
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      // Faire une modification
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      // Cliquer sur Retour
      const retourButton = screen.getByRole('button', { name: /retour/i });
      await user.click(retourButton);
      
      // Annuler dans le modal
      const annulerButton = screen.getByRole('button', { name: /annuler/i });
      await user.click(annulerButton);
      
      // Le formulaire devrait toujours être visible
      expect(screen.getByLabelText(/client/i)).toBeInTheDocument();
    });
  });

  describe('États de chargement', () => {
    it('devrait afficher un indicateur de chargement', () => {
      renderWithProviders(<FactureForm {...defaultProps} mode="edit" idFacture={1} />);
      
      expect(screen.getByTestId('loading-spinner') || screen.getByText(/chargement/i)).toBeInTheDocument();
    });

    it('devrait désactiver les boutons pendant le chargement', () => {
      renderWithProviders(<FactureForm {...defaultProps} mode="edit" idFacture={1} />);
      
      const submitButton = screen.queryByRole('button', { name: /enregistrer|modifier/i });
      if (submitButton) {
        expect(submitButton).toBeDisabled();
      }
    });
  });

  describe('Gestion des erreurs', () => {
    it('devrait afficher une erreur en cas d\'échec de chargement', async () => {
      mockFactureService.getFacture.mockRejectedValueOnce(new Error('Erreur réseau'));
      
      renderWithProviders(<FactureForm {...defaultProps} mode="edit" idFacture={1} />);
      
      await waitFor(() => {
        expect(screen.getByText(/erreur/i)).toBeInTheDocument();
      });
    });

    it('devrait afficher une erreur en cas d\'échec de sauvegarde', async () => {
      mockFactureService.creerFacture.mockRejectedValueOnce(new Error('Erreur de sauvegarde'));
      
      const { user } = renderWithProviders(<FactureForm {...defaultProps} />);
      
      // Remplir le formulaire minimalement
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      await submitForm(user, 'Enregistrer');
      
      await waitFor(() => {
        expect(screen.getByText(/erreur.*sauvegarde/i)).toBeInTheDocument();
      });
    });
  });
});